.home {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;

    background-image: url("big-background.jpg");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;

    text-align: center;

    @media (max-width: 1440px) {
        background-image: url("background-1.jpg");
    }
}

.home__title {
    color: white;
    font-weight: 500;
    font-size: 72px;

    margin: 0;
}

.home__subtitle {
    margin-top: 40px;
    font-size: 48px;
    color: white;
}