.sidebar {
    height: 100vh;
    padding-top: 55px;
    padding-bottom: 20px;
    width: 250px;
    background: #333333;
    text-align: center;
}

.sidebar__img-wrapper {
    width: 150px;
    height: 150px;
    margin-left: 50px;
    border-radius: 50%;
    overflow: hidden;
    background: #eee;
}


.sidebar__img {
    width: 100%;
    height: 100%;
    max-width: 100%;
    object-fit: cover;
}

.sidebar__name {
    margin-top: 20px;

    font-weight: normal;
    font-size: 24px;
    text-align: center;
    color: white;
}

.sidebar__nav {
    margin-top: 60px;
}

.sidebar__link {
    display: block;
    padding: 18px 0;
    margin: 1px 1px 0 1px;

    background: #232323;
    color: #FFFFFF;

    font-family: Roboto;
    font-size: 24px;
    text-decoration: none;

    p {
        margin-top: 5px;
    }
}

.sidebar__link--active {
    background: #22408D;
}

.sidebar__socials {
    margin-top: 190px;
}

.sidebar__social-list {
    display: flex;
    justify-content: center;

    li {
        margin-right: 28px;
        &:last-of-type {
            margin-right: 0;
        }
    }
}