.projects {
    flex: 1;
    text-align: center;
}

.projects__title {
    margin-top: 200px;
    font-weight: 500;
    font-size: 48px;
}

.projects__list {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    max-width: 1100px;
    margin: auto;
    margin-top: 45px;
    gap: 43px 25px;
}

.projects__item {
    width: 350px;
    height: 266px;
    box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.25);
    transition: transform 0.20s ease-in;

    &:hover,
    &:focus {
        transform: translateY(-5%);
    }

    a {
        display: block;
        width: 100%;
        height: 100%;
    }

    img {
        width: 100%;
        height: 100%;
        max-width: 100%;
    }
}