.skills {
    flex: 1;
    text-align: center;
    height: 100vh;
    overflow: auto;
}

.skills__wrapper {
    margin: 200px 99px 0 99px;
}

.skills__title {
    font-weight: 500;
    font-size: 48px;
}

.skills__list {
    margin-top: 35px;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 38px 45px;
    padding-bottom: 50px;

}

.skills__item {
    display: block;
    width: 300px;
    padding: 38px 0 22px;

    box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.25);
    transition: transform 0.15s ease-in-out;

    &:hover {
        transform: translateY(-8%);
    }
}

.skills__skill-title {
    margin-top: 8px;

    font-weight: 500;
    font-size: 24px;
    line-height: 28px;

    color: #5B5B5B;
}